<template>
<nav>
  <a href="/addImage"  class="add">Add Image</a>
  <div id="map"></div>
  <img v-bind:src="'data:image/jpeg;base64,'+images[0].url" alt="image"/>


</nav>
</template>

<script>
import axios from 'axios'
import YandexMap from '../mapScript'

export default {

  data() {
    return {
      error: false,
      coords: [],
      images: [],
      data: []
    }
  },
  mounted() {
    this.$nextTick(function () {
      axios.get('/api/image?page='+0).then(response => {
        this.images=response.data.data
      }).catch((error) => {
        console.log(error.response.status)
      })

      ymaps.ready(() => {
        const myMap = new ymaps.Map('map', {
          center: [53.893009, 27.567444],
          zoom: 10
        });

        const objectManager = new ymaps.ObjectManager({
          // Чтобы метки начали кластеризоваться, выставляем опцию.
          clusterize: true,
          // ObjectManager принимает те же опции, что и кластеризатор.
          gridSize: 32,
          clusterDisableClickZoom: true
        });

        // Чтобы задать опции одиночным объектам и кластерам,
        // обратимся к дочерним коллекциям ObjectManager.
        objectManager.objects.options.set('preset', 'islands#greenDotIcon');
        objectManager.clusters.options.set('preset', 'islands#greenClusterIcons');
        myMap.geoObjects.add(objectManager);
        for (let i = 0; i <this.images.length ; i++) {
          const image = JSON.stringify({type: 'Feature', id: this.images[i].id, geometry: {type: 'Point',
              coordinates: [this.images[i].lat,this.images[i].lng]} })
          this.data.push(image)
          console.log(image)
        }

        console.log(this.data)
        objectManager.add(this.data)



        myMap.events.add('click', (e) => {
          if (!myMap.balloon.isOpen()) {
            this.coords = e.get('coords');
            myMap.balloon.open(this.coords, {
              contentHeader: 'Photo was taken here!',
              contentBody:
                '<p>Coordinates: ' + [
                  this.coords[0].toPrecision(6),
                  this.coords[1].toPrecision(6)
                ]
            });
          } else {
            myMap.balloon.close();
          }
        })
      })





    })
  },
  methods: {

  }
}
</script>

<style>
#map {
  width: 600px;
  height: 400px;
  margin-left: 35%;
}

.info_paragraph {
  font-size: 25px;
}
.add {
  color: blue;
  text-decoration: none;
}
</style>
